import config from "../config";
import module from "../index";
export default {
  props: {
    flight: {
      type: Object,
      required: true
    },
    pos: {
      type: Object,
      required: true
    }
  },
  data() {
    return {
      module,
      config,
      baseX: this.pos.end.x - this.pos.padding.x,
      baseY: this.pos.end.y - this.pos.flight.height + this.pos.padding.y,
      date: dayjs(this.flight.arrivalDate).format("HH:mm")
    };
  }
};